<template>
  <div class="change-password-box">
    <el-form ref="ruleFormRef" :model="form" :rules="rules">
      <el-form-item prop="oldPassword" label="旧密码">
        <el-input autocomplete="off" type="password" v-model="form.oldPassword" placeholder="请输入密码" show-password />
      </el-form-item>
      <el-form-item prop="newPassword" label="新密码">
        <el-input autocomplete="off" type="password" v-model="form.newPassword" placeholder="请输入密码" show-password />
      </el-form-item>
      <el-form-item prop="newPassword2" label="确认新密码">
        <el-input autocomplete="off" type="password" v-model="form.newPassword2" placeholder="请输入密码" show-password />
      </el-form-item>
      <div class="input-tips" v-if="showTips" :style="{ top: showError ? '0rem' : '-10rem' }">请输入8-20位的字母、数字或组合，不支持特殊符号
      </div>
      <el-form-item class="form-submit">
        <div class="btn-primary">
          <el-button @click="submitForm(ruleFormRef)">确定</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive, watch } from 'vue'
import type { FormRules, FormInstance } from 'element-plus'
import { useRouter } from 'vue-router'
import store from '@/store'
import { modifyUserPassword } from '@/api'

const router = useRouter()
const ruleFormRef = ref<FormInstance>()
const showError = ref(false)
const showTips = ref(true)
const userInfo: any = ref({})
// do not use same name with ref
const form = reactive({
  newPassword: '',
  oldPassword: '',
  newPassword2: '',
})
interface RuleForm {
  newPassword: string,
  oldPassword: string,
  newPassword2: string,
}

const validatePass = (rule: any, value: any, callback: any) => {
  const reg = /^[a-zA-Z0-9]+$/
  if (!value) {
    showError.value = true;
    callback(new Error('密码不能为空'))
    return
  }
  if (!reg.test(value) || value.length < 2 || value.length > 20) {
    callback(new Error('请输入8-20位的字母、数字或组合，不支持特殊符号'))
    showTips.value = false;
    showError.value = true;
    return
  } else {
    showTips.value = true;
  }
  showError.value = false;
  callback()
}

const rules = reactive<FormRules<RuleForm>>({
  newPassword: [{ required: true, validator: validatePass, trigger: 'blur' }],
  oldPassword: [{ required: true, validator: validatePass, trigger: 'blur' }],
  newPassword2: [{ required: true, validator: validatePass, trigger: 'blur' }],
})

const submitForm = async (formEl: FormInstance | undefined) => {
  if (!formEl) return
  await formEl.validate(async (valid, fields: any) => {
    if (valid) {
      console.log(userInfo.value)
      modifyUserPassword({ userId: userInfo.value.id, password: form.newPassword2 }).then((res: any) => {
        if (res.code == '200') {
          router.push('/login')
        }
      })
    } else {
      console.log('error submit!', fields)
      showError.value = !!fields.newPassword2;
    }
  })
}

onMounted(() => {
  store.state.pageContentTitle = ['个人中心', '修改密码'];
  userInfo.value = store.state.userData;
})
watch(() => store.state.userData, (newState) => {
  userInfo.value = newState;
})
</script>
<style scoped lang="less">
.change-password-box {
  width: 1184rem;
  height: 357rem;
  background: #FFFFFF;
  border-radius: 10rem;
  margin-top: 24rem;
  padding: 40rem 28rem;
  box-sizing: border-box;

  ::v-deep(.el-form-item__label) {
    width: 96rem;
    height: 32rem;
    line-height: 32rem;
    text-align: left;
    justify-content: flex-start;
    font-size: 14rem;
  }

  ::v-deep(.el-input) {
    width: 330rem;
    font-size: 14rem;
  }

  ::v-deep(.el-input__wrapper) {
    height: 32rem;
  }

  ::v-deep(.el-form-item) {
    margin-bottom: 18rem;
  }

  ::v-deep(.el-form-item__error) {
    font-size: 12rem;
  }

  .input-tips {
    font-weight: 400;
    font-size: 14rem;
    color: #797979;
    line-height: 22rem;
    text-align: left;
    padding-left: 95rem;
    position: relative;
    margin-top: -5rem;
  }

  .form-submit {
    width: 84rem;
    height: 32rem;
    background: #3991F2;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 25rem;
    margin-left: 155rem;
    margin-top: 40rem;

    .btn-primary {
      width: 100%;
      height: 32rem;
      font-size: 0;

      &>button {
        width: 100%;
        height: 32rem;
        background-color: transparent;
        border-radius: 16rem;
        border: none;
        color: #fff;
        font-weight: 400;
        font-size: 14rem;
        color: #FFFFFF;
        margin-top: 1rem;
      }
    }
  }
}
</style>