<template>
  <div class="home-wrap">
    <table-list ref="tableRefs" :delete-dataEvent="deleteDataEvent" :data="tableData" :column-list="columnList"
      :change-page="changePage" :delete-content="deleteContent" :data-total="pageTotal" :page-size="searchForm.pageSize"
      :page-no="searchForm.pageNo"></table-list>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive } from 'vue'
import TableList from '@/components/TableList.vue'
import store from '@/store'
import { queryStuPracticeInfo } from '@/api'
import { useRouter } from 'vue-router'

const router = useRouter()
const deleteContent = {
  title: '删除实训信息',
  content: '确认删除当前实训信息？'
}
const searchForm: any = reactive({ // 查询对象
  pageSize: 10,
  pageNo: 1,
  userCode: ''
})
const pageTotal = ref(10) // 总计数据条数
const tableRefs = ref()
const tableData: any = ref([])

const editData = (data: any) => {
  //
}
const columnList: any = [
{
    prop: 'imgKey',
    label: '实训封面',
    type: "image",
  },
  {
    prop: 'practiceName',
    label: '实训名称'
  },
  {
    prop: 'secondMajor',
    label: '专业'
  },
  {
    prop: 'className',
    label: '所属班级'
  },
  {
    prop: 'principalPerson',
    label: '负责人'
  },
  {
    prop: 'operate',
    label: '操作',
    width: 136,
    hideEdit: true,
    hideDelete: true,
    showPreview: '/student-details',
    events: [editData]
  }
]
const deleteDataEvent = (data: any) => {
  //
}

const changePage = (cur: any) => {
  searchForm.pageNo = cur;
  getDataList()
}

const getDataList = () => {
  queryStuPracticeInfo({ ...searchForm }).then((res) => {
    tableData.value = [...res.data.records]
    pageTotal.value = res.data.total;
  })
}

onMounted(() => {
  store.state.pageContentTitle = ['实训列表'];
  searchForm.userCode = window.localStorage.getItem('userCode') || '';
  getDataList()
})
</script>
<style scoped lang="less">
.home-wrap {
  margin-top: 24rem;
}
</style>