import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import StudentTrainingListView from "@/views/StudentTrainingListView.vue";
import LoginView from "@/views/LoginView.vue";
import UserView from "@/views/UserView.vue";
import StudentDetails from "@/views/details/StudentDetails.vue";
import ChangePassword from "@/views/details/ChangePassword.vue";
import TeacherTrainingListView from "@/views/TeacherTrainingListView.vue";
import TeacherDetails from "@/views/details/TeacherDetails.vue";
import CreateTraining from "@/views/create/CreateTraining.vue";
import TrainingResults from "@/views/TrainingResults.vue";
import AccountManagement from "@/views/AccountManagement.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/login",
    name: "login",
    alias: "/",
    meta: {
      hideSide: true,
    },
    component: LoginView,
  },
  {
    path: "/student-training-list",
    name: "student-training-list",
    component: StudentTrainingListView,
  },
  {
    path: "/user",
    name: "user",
    component: UserView,
  },
  {
    path: "/student-details",
    name: "student-details",
    component: StudentDetails,
  },
  {
    path: "/change-password",
    name: "change-password",
    component: ChangePassword,
  },
  {
    path: "/teacher-training-list",
    name: "teacher-training-list",
    component: TeacherTrainingListView,
  },
  {
    path: "/teacher-details",
    name: "teacher-details",
    component: TeacherDetails,
  },
  {
    path: "/create-training",
    name: "create-training",
    meta: {
      hideSide: true,
      maxPadding: true,
    },
    component: CreateTraining,
  },
  {
    path: "/training-results",
    name: "training-results",
    component: TrainingResults,
  },
  {
    path: "/account-management",
    name: "account-management",
    component: AccountManagement,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
