<template>
  <div class="table-module">
    <el-table :data="data" ref="tableRef" @select="handleSelectionChange" @select-all="handleSelectionChange"
      :style="{ width: '100%', maxHeight: '838px', height: tableHeight }" class="styleTable"
      @filter-change="filterChange">
      <el-table-column v-for="item in columnList" :prop="item.prop" :key="item.prop" :label="item.label"
        :width="item.width" :filters="item.filters" :column-key="item.prop"
        :filter-method="item.filters ? filterHandler : null" :filter-multiple="false" filter-class-name="filter-style">
        <template #filter-icon>
          <img v-if="filterData[item.resetKey || item.prop] != 0 && !filterData[item.resetKey || item.prop]"
            class="filter-icon"
            :style="{ paddingLeft: item.label.length * 12 + 2 + 'px', marginLeft: -item.label.length * 12 + 'px' }"
            src="@/assets/images/icon/filter-icon.png" @click="resetFilterStyle(item.label)" />
          <img v-if="filterData[item.resetKey || item.prop] || filterData[item.resetKey || item.prop] == 0"
            class="filter-icon"
            :style="{ paddingLeft: item.label.length * 12 + 2 + 'px', marginLeft: -item.label.length * 12 + 'px' }"
            src="@/assets/images/icon/filter-iconA.png" @click="resetFilterStyle(item.label)" />
        </template>
        <template #default="scope">
          <span v-if="item.list">{{ item.list[scope.row[item.prop]] }}</span>
          <span v-if="item.prop == 'codeType'">{{ scope.row[item.resetProp[0]] || scope.row[item.resetProp[1]] }}</span>
          <img v-if="item.type === 'image' && scope.row[item.prop]" :src="baseUrl + '/api/file/download/' + scope.row[item.prop]" width="72"
            height="46" />
          <span v-if="item.prop == 'firstMajor'">{{ scope.row['userType'] == 2 ? (scope.row[item.prop] || '铁道专业') :
            scope.row[item.prop] }}</span>
          <el-button style="color: #037BFF;" v-if="item.prop === 'operate' && item.showPreview" link type="primary"
            @click="router.push(item.showPreview + '?id=' + scope.row.id)">查看</el-button>
          <el-button style="color: #037BFF;" v-if="item.prop === 'operate' && item.showDetail" link type="primary"
            @click="item.showDetail">查看</el-button>
          <el-button style="color: #037BFF;" v-if="item.prop === 'operate' && !item.hideEdit" link type="primary"
            @click="null">编辑</el-button>
          <el-button link class="popover-btn" v-if="item.prop === 'operate' && !item.hideDelete"
            @click="handleDelete($event, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div v-if="!!changePage && data.length" class="pagination">
      <el-pagination background layout="prev, pager, next" :total="dataTotal" :current-page="pageNo"
        @current-change="changePageEvent" :pager-count="5" :page-size="pageSize">
      </el-pagination>
    </div>
  </div>
  <tips-view v-if="currentData" :tips-title="deleteContent.title" :show-btn="'确定'" :sure-event="deleteEvent"
    :cancle-event="() => currentData = null">
  </tips-view>
</template>

<script lang="ts" setup>
import { ref, reactive, computed, onMounted, watch, nextTick } from 'vue'
import store from "@/store";
import router from '@/router';
import TipsView from './TipsView.vue';

const tableRef = ref()
const selectDataArr: any = ref(null) // 多选被选中的数据
const currentData: any = ref(null)
const baseUrl = process.env.NODE_ENV === "production" ? "http://172.26.136.103:18098" : "";

const props = defineProps({
  data: {
    default: null,
    type: Object
  },
  columnList: {
    default: null,
    type: Object
  },
  changePage: {
    default: null,
    type: Function
  },
  changeBg: {
    default: null,
    type: String
  },
  dataTotal: {
    default: 0,
    type: Number
  },
  deleteContent: {
    default: null,
    type: Object
  },
  deleteDataEvent: {
    default: null,
    type: Function
  },
  pageSize: {
    default: 20,
    type: Number
  },
  pageNo: {
    default: 1,
    type: Number
  },
  handleFilter: {
    default: null,
    type: Function
  },
})

const tableHeight = ref('auto')
const filterData: any = ref({})

const changePageEvent = (e: any) => {
  props.changePage && props.changePage(e);
}

const resetFilterStyle = (lable: string) => {
  const filterDoms: any = document.querySelectorAll('.filter-style');
  if (filterDoms.length) {
    setTimeout(() => {
      filterDoms.forEach((dom: any) => {
        if (dom.getBoundingClientRect().width) {
          if (lable.length == 2) {
            dom.style.marginLeft = '-66px'
          }
        }
      })
    })
  }
}


const handleDelete = (event: any, data: any) => {
  currentData.value = data;
  store.state.showTips = props.deleteContent.content;
}

const deleteEvent = () => {
  props.deleteDataEvent(currentData.value);
  currentData.value = null
  store.state.showTips = ''
}

const changeTableSize = () => {
  const elScrollbarViewHeight: any = document.querySelector('.el-scrollbar__view')?.getBoundingClientRect().height;
  const addHeight: any = document.querySelector('#app')?.getBoundingClientRect().height;
  const remSize = parseFloat(getComputedStyle(document.documentElement).fontSize);
  const subValue = remSize * 132 + 144;
  if (elScrollbarViewHeight > addHeight - subValue) {
    tableHeight.value = `calc(100vh - ${subValue}px)`
  }
}

const handleSelectionChange = (val: any) => {
  if (selectDataArr.value) {
    selectDataArr.value[props.pageNo] = [...val];
  } else {
    selectDataArr.value = {
      [props.pageNo]: [...val]
    };
  }
  let sumSelectData = 0;
  Object.values(selectDataArr.value).forEach((e: any) => sumSelectData += e.length);
  if (!sumSelectData) {
    selectDataArr.value = null;
  }
}

const filterHandler = (value: string, row: any, column: any) => {
  return true
}

const filterChange = (newFilters: any) => {
  const currentKey = Object.keys(newFilters)[0];
  const currentObj = props.columnList.find((item: any) => item.prop == currentKey);
  if (currentKey) {
    props.handleFilter(currentObj.resetKey || currentKey, newFilters[currentKey]);
    filterData.value[currentObj.resetKey || currentKey] = newFilters[currentKey][0];
  }
}

onMounted(() => {
  changeTableSize()
  window.addEventListener('resize', () => {
    changeTableSize()
  }, false)
})

// 多选按钮的选择和记录选择状态
watch(() => props.data, (newState) => {
  nextTick(() => {
    changeTableSize()
  })
  // setTimeout(() => {
  //   changeTableSize()
  // })
})

defineExpose({
  selectDataArr
})
</script>
<style scoped lang="less">
.el-dialog__header {
  display: flex;
}

.el-dialog__body {
  text-align: left;
  padding: 20px !important;
}

.table-module {
  margin: 0 0 20px;

  .filter-icon {
    width: 16px;
    height: 16px;
    padding-left: 2px;
    margin-top: -3px;
  }

  ::v-deep(input[aria-hidden="true"]) {
    display: none !important;
  }

  ::v-deep(.el-radio:focus:not(.is-focus):not(:active):not(.is-disabled) .el-radio__inner) {
    box-shadow: none !important;
  }
}

:deep(.el-dialog__title) {
  font-weight: bold;
  font-size: 18px;
  color: #1E1E1E;
}

.pagination {
  position: relative;
  margin: 32px 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.styleTable {
  background-color: transparent !important;
  border: 1px solid #E6EDF7;
  border-radius: 4px;
  overflow: hidden;
  overflow-y: auto;
  height: calc(100vh - 322px);

  ::v-deep(.el-table__inner-wrapper::before) {
    content: none;
  }
}

::v-deep(.el-table tr:last-child td.el-table__cell) {
  border-bottom-color: transparent;
}

.popover-btn {
  color: #F23333 !important;
}

.styleTable::v-deep(.el-table th.el-table__cell),
::v-deep(.el-table tr) {
  height: 78px;
  font-weight: 400;
  font-size: 14px;
  color: #1E1E1E;
}

.styleTable::v-deep(.el-table__header-wrapper th) {
  background-color: #EFEFEF !important;
  font-weight: 400;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
}

.styleTable::v-deep(.el-table__header-wrapper tr) {
  height: 56px;
}


::v-deep(.el-table th) {
  text-align: center;
}

// ::v-deep(.el-table th:first-child) {
//   text-align: left;
// }

// ::v-deep(.el-table th:last-child) {
//   text-align: right;
// }

::v-deep(.el-table td) {
  text-align: center;
}

// ::v-deep(.el-table td:first-child) {
//   text-align: left;
// }

// ::v-deep(.el-table td:last-child) {
//   text-align: right;
// }
</style>