<template>
  <div class="user-box">
    <div class="profile-img"></div>
    <div class="profile-info" v-if="userType != 3">
      <div>
        <div>姓名：</div>
        <div>{{ userInfo.userName }}</div>
      </div>
      <div>
        <div>性别：</div>
        <div>{{ userInfo.gender }}</div>
      </div>
      <div>
        <div>所属专业类：</div>
        <div>{{ userInfo.firstMajor || '铁道专业' }}</div>
      </div>
      <div v-if="userType == 2">
        <div>专业：</div>
        <div>{{ userInfo.secondMajor }}</div>
      </div>
      <div v-if="userType == 2">
        <div>年级：</div>
        <div>{{ userInfo.grade }}级</div>
      </div>
      <div v-if="userType == 2">
        <div>班级：</div>
        <div>{{ userInfo.className }}</div>
      </div>
    </div>
    <div class="profile-info" :style="{ marginLeft: userType == 3 ? 0 : 'auto' }"
      :class="userType == 2 ? 'is-student' : ''">
      <div>
        <div>账号/学号：</div>
        <div>{{ userInfo.teacherCode || userInfo.stuCode || userInfo.userName }}</div>
      </div>
      <div>
        <div>密码：</div>
        <div>{{ userInfo.password }}</div>
      </div>
      <div>
        <div class="change-password" @click="router.push('/change-password')">修改密码</div>
        <div class="exit-btn" @click="router.push('/')">退出</div>
      </div>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, watch } from 'vue'
import store from '@/store'
import router from '@/router';
import { getUser } from '@/api'

const userType = ref(0)
const userInfo: any = ref({})

onMounted(() => {
  store.state.pageContentTitle = ['个人中心']
  userInfo.value = { ...store.state.userData };
  userType.value = store.state.userType;
})

watch(() => store.state.userType, (newState) => {
  userType.value = newState;
})

watch(() => store.state.userData, (newState) => {
  userInfo.value = { ...newState };
})
</script>
