  background: #FFFFFF;
  border-radius: 10rem;
  margin-top: 24rem;
  padding: 40rem;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  .profile-img {
    width: 140rem;
    height: 140rem;
    background: #D9D9D9;
    border-radius: 100%;
    margin-right: 47rem;
  }

  .profile-info {
    width: 260rem;
    height: 180rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-weight: 400;
    font-size: 14rem;
    color: #797979;
    line-height: 20rem;

    &>div {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      line-height: 20rem;
      margin-bottom: 12rem;

      &>div:first-child {
        width: 84rem;
        margin-right: 6rem;
      }
    }

    &.is-student {
      justify-content: flex-start;
    }
  }

  &>.profile-info:last-child {
    margin-left: 336rem;

    .change-password, .exit-btn {
      width: 76rem;
      height: 30rem;
      background: #3991F2;
      box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
      border-radius: 25rem;
      font-weight: 400;
      font-size: 12rem;
      color: #FFFFFF;
      line-height: 30rem;
      text-align: center;
      display: inline-block;
      margin-top: 6rem;
      cursor: pointer;
      margin-left: 90rem;
    }

    .exit-btn {
      width: 56rem;
      margin-left: 10rem;
      background-color: #8a8888;
    }
  }
}
</style>