<template>
  <div class="tips-model" v-if="store.state.showTips">
    <div class="tips-content">
      <div class="title">{{ tipsTitle }}</div>
      <div>{{ store.state.showTips }}</div>
      <div v-if="!showBtn" class="btn" @click="handleCancel">知道了</div>
      <div v-if="showBtn" class="submit-btns">
        <div @click="handleCancle">取消</div>
        <div @click="handleSure">{{ showBtn }}</div>
      </div>
      <div class="close" @click="store.state.showTips = ''"></div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed, defineProps } from 'vue';
import store from '@/store';
const props = defineProps({
  tipsTitle: {
    default: '提示',
    type: String
  },
  showBtn: {
    default: '',
    type: String
  },
  sureEvent: {
    default: null,
    type: Function
  },
  cancleEvent: {
    default: null,
    type: Function
  },
})

const handleCancle = () => {
  props.cancleEvent && props.cancleEvent()
  store.state.showTips = ''
}

const handleSure = () => {
  props.sureEvent && props.sureEvent()
}

const handleCancel = () => {
  if (store.state.showTips) {
    store.state.showTips = ''
  }
}

</script>

