    }

    .login_tip {
      font-weight: 400;
      font-size: 14px;
      color: #797979;
    }

    .btn {
      width: 112px;
      height: 36px;
      background: linear-gradient(225deg, #0375FF 0%, #3C96FF 100%);
      box-shadow: inset -1px -1px 0px 0px rgba(255, 255, 255, 0.2), inset 1px 1px 0px 0px rgba(11, 91, 225, 0.4);
      border-radius: 4px;
