      text-align: center;
      font-weight: 600;
      font-size: 14px;
      color: #FFFFFF;
      position: absolute;
      right: 24px;
      bottom: 31px;
      cursor: pointer;
      letter-spacing: 1px;
    }

    .login_btn {
      position: relative;
      right: 0;
      bottom: 0;
      margin-top: 24px;
      background: #D6DFF1;
      border-radius: 4px 4px 4px 4px;
      font-weight: bold;
      font-size: 14px;
