<template>
  <div class="detail-box">
    <div class="detail-title">{{ practiceInfo.practiceName }}</div>
    <div class="detail-content">
      <div class="detail-img"></div>
      <div class="detail-list">
        <div>
          <div v-for="(item, index) in detailList" :key="index">
            <div v-for="(e, i) in item" :key="i">
              <span class="detail-list-label">{{ e.label }}</span>
              <span v-if="e.value">{{ e.value }}</span>
              <span v-if="e.values" class="show-data-list">
                <span v-for="(k, x) in e.values" :key="x">{{ k }}</span>
              </span>
            </div>
          </div>
        </div>
        <div>
          {{ practiceInfo.practiceIntriduction }}
        </div>
      </div>
    </div>
    <div class="to-train" @click="toTraining">我要做实训</div>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref } from 'vue'
import store from '@/store'
import { useRouter } from 'vue-router'
import { queryPracticeInfoById, getPracticeRelateInfoStr } from '@/api'

const router = useRouter()
const detailList: any = ref([])
const practiceInfo: any = ref({})
const practiceId = ref('')

const toTraining = () => {
  getPracticeRelateInfoStr({ practiceId: practiceId.value }).then((res: any) => {
    console.log(res)
  })
}

onMounted(() => {
  const pageQuery: any = router.currentRoute.value.query;
  store.state.pageContentTitle = ['实训列表', '实训详情'];
  if (pageQuery.id) {
    practiceId.value = pageQuery.id;
    queryPracticeInfoById({ id: pageQuery.id }).then((res) => {
      practiceInfo.value = { ...res.data };
      detailList.value = [
        [
          {
            label: '负责人：',
            value: res.data.principalPerson
          },
          {
            label: '实训类型：',
            value: res.data.practiceType
          },
          {
            label: '对应专业：',
            values: res.data.secondMajor.split('、'),
          },
        ],
        [
          {
            label: '上线时间：',
            value: res.data.createTime?.slice(0, 10)
          },

          {
            label: '所属专业类：',
            value: res.data.firstMajor
          },
          {
            label: '对应班级：',
            values: res.data.className.split('、'),
          },
        ]
      ]
    })
  }
})
</script>
<style scoped lang="less">
.detail-box {
  position: relative;
  width: 100%;
  height: 357rem;
  background: #FFFFFF;
  border-radius: 10rem;
  margin-top: 24rem;
  padding: 24rem;
  box-sizing: border-box;

  .to-train {
    width: 118rem;
    height: 32rem;
    background: #3991F2;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 25rem;
    color: #fff;
    font-weight: 400;
    font-size: 14rem;
    line-height: 32rem;
    cursor: pointer;
    text-align: center;
    position: absolute;
    right: 24rem;
    top: 14rem;
  }

  .create-training {
    position: absolute;
    right: 0;
    top: -50rem;
    width: 104rem;
    height: 32rem;
    line-height: 32rem;
    text-align: center;
    background: #3991F2;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 25rem;
    font-weight: 400;
    font-size: 14rem;
    color: #FFFFFF;
    cursor: pointer;
  }

  .detail-content {
    height: 245rem;
    margin-top: 20rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .detail-img {
      width: 382rem;
      height: 237rem;
      background: #D9D9D9;
    }

    .detail-list {
      width: 707rem;
      font-weight: 400;
      font-size: 14rem;
      line-height: 20rem;
      color: #939393;

      &>div:first-child {
        height: 146rem;
        margin-bottom: 22rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-weight: 400;
        font-size: 14rem;
        color: #797979;
        line-height: 20rem;
        // padding-right: 100rem;

        &>div {
          width: 45%;
          height: 100%;
          display: flex;
          justify-content: flex-start;
          flex-direction: column;
          align-items: flex-start;

          &>div {
            margin-bottom: 12rem;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            width: 100%;
          }

          .detail-list-label {
            width: 84rem;
            display: inline-block;
            margin-right: 6rem;
          }

          .show-data-list {
            width: calc(100% - 91rem);
            display: inline-block;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;

            &>span {
              margin-bottom: 6rem;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}
</style>