<template>
  <div class="home-wrap">
    <div class="create-training" @click="router.push('/create-training')">新建实训</div>
    <table-list ref="tableRefs" :delete-dataEvent="deleteDataEvent" :data="tableData" :column-list="columnList"
      :change-page="changePage" :delete-content="deleteContent" :data-total="pageTotal" :page-size="searchForm.pageSize"
      :page-no="searchForm.pageNo" :handle-filter="handleFilter"></table-list>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive } from 'vue'
import TableList from '@/components/TableList.vue'
import store from '@/store'
import { useRouter } from 'vue-router'
import { queryTeacherPracticeInfo, deletePracticeInfo, getPracticeNames, getExistClass } from '@/api'

const router = useRouter()
const deleteContent = {
  title: '删除实训信息',
  content: '确认删除当前实训信息？'
}
const searchForm: any = reactive({ // 查询对象
  pageSize: 10,
  pageNo: 1,
  userCode: ''
})
const pageTotal = ref(10) // 总计数据条数
const tableRefs = ref()
const tableData: any = ref([])

const editData = (data: any) => {
  //
}
const columnList: any = ref([
  {
    prop: 'imgKey',
    label: '实训封面',
    type: "image",
  },
  {
    prop: 'practiceName',
    label: '实训名称',
    filters: []
  },
  {
    prop: 'secondMajor',
    label: '专业'
  },
  {
    prop: 'className',
    label: '所属班级',
    filters: []
  },
  {
    prop: 'principalPerson',
    label: '负责人'
  },
  {
    prop: 'operate',
    label: '操作',
    width: 136,
    hideEdit: true,
    showPreview: '/teacher-details',
    events: [editData]
  }
])

const handleFilter = (key: string, value: any) => {
  searchForm[key] = value ? value[0] : null;
  searchForm.pageNo = 1;
  getDataList();
  tableRefs.value.selectDataArr = null;
}

const deleteDataEvent = (data: any) => {
  deletePracticeInfo({ id: data.id }).then((res: any) => {
    if (res.code == '200') {
      getDataList()
    }
  })
}

const changePage = (cur: any) => {
  searchForm.pageNo = cur;
  getDataList()
}

const getDataList = () => {
  queryTeacherPracticeInfo({ ...searchForm }).then((res) => {
    tableData.value = [...res.data.records]
    pageTotal.value = res.data.total;
  })
}

onMounted(() => {
  store.state.pageContentTitle = ['实训列表'];
  searchForm.userCode = window.localStorage.getItem('userCode') || '';
  getDataList()

  getExistClass().then((res) => {
    columnList.value[3].filters = res.data.map((e: any) => ({ text: e, value: e }));
  })
  getPracticeNames().then((res) => {
    columnList.value[1].filters = res.data.filter((v: any) => !!v).map((e: any) => ({ text: e, value: e }))
  })
})
</script>
<style scoped lang="less">
.home-wrap {
  position: relative;
  margin-top: 24rem;

  .create-training {
    position: absolute;
    right: 0;
    top: -50rem;
    width: 104rem;
    height: 32rem;
    line-height: 32rem;
    text-align: center;
    background: #3991F2;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 25rem;
    font-weight: 400;
    font-size: 14rem;
    color: #FFFFFF;
    cursor: pointer;
  }
}
</style>