<template>
  <div class="create-wrap">
    <div class="form-box">
      <el-form ref="ruleFormRef" :model="form" :rules="rules" label-width="110rem">
        <el-form-item prop="practiceName" label="实训课程名称">
          <el-input v-model="form.practiceName" placeholder="请输入实训课程名称" />
        </el-form-item>
        <el-form-item prop="practiceType" label="实训课程类型">
          <div class="practice-type-text">虚拟仿真实验</div>
        </el-form-item>
        <el-form-item prop="practicePath" label="实训课程链接">
          <el-input v-model="form.practicePath" placeholder="请输入实训课程链接" />
        </el-form-item>
        <el-form-item prop="imgKey" label="上传封面图">
          <div class="cover-image-wrap">
            <div class="cover-image">
              <img v-if="uploadedImg" class="img-icon full-img" :src="uploadedImg" />
              <img v-else class="img-icon" :src="defaultImg" />
            </div>
            <el-upload class="upload-btn" :action="'/api/file/uploadFile'" :show-file-list="false"
              :on-success="uploaded" @click.stop="null">
              <el-button>选择</el-button>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item label="实训简介" prop="practiceIntriduction" class="intriduction-text">
          <el-input :rows="5" type="textarea" v-model="form.practiceIntriduction" resize="none" placeholder="请输入实训简介" />
        </el-form-item>
        <el-form-item prop="firstMajor" label="所属专业类">
          <el-input v-model="form.firstMajor" placeholder="请输入所属专业类" />
        </el-form-item>
        <el-form-item prop="secondMajor" label="对应专业">
          <el-select v-model="form.secondMajor" placeholder="请输入对应专业" class="select-default" multiple
            popper-class="select-option">
            <el-option v-for="(item, index) in majorList" :key="index" :label="item" :value="item" />
          </el-select>
        </el-form-item>
        <el-form-item prop="className" label="对应班级">
          <el-select v-model="form.className" placeholder="请输入对应班级" class="select-default" popper-class="select-option"
            multiple>
            <el-option v-for="(item, index) in classList" :key="index" :label="item" :value="item" />
          </el-select>
        </el-form-item>
        <el-form-item prop="principalPerson" label="负责人">
          <el-input v-model="form.principalPerson" placeholder="请输入负责人" />
        </el-form-item>
        <div class="form-submit">
          <div class="btn-primary">
            <el-button @click="submitForm(ruleFormRef)">保存</el-button>
          </div>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive, watch } from 'vue'
import store from '@/store'
import type { FormRules, FormInstance } from 'element-plus'
import { useRouter } from 'vue-router'
import { getExistSecondMajor, getExistClass, addPracticeInfo, queryPracticeInfoById, updatePracticeInfo } from '@/api'

const router = useRouter()
const ruleFormRef = ref<FormInstance>()
const trainingId = ref(0)
const majorList: any = ref([])
const classList: any = ref([])
const defaultImg = require('@/assets/images/icon/picture-icon.png');
const uploadedImg = ref('')
const baseUrl = process.env.NODE_ENV === "production" ? "http://172.26.136.103:18098" : "";

const form: any = reactive({
  practiceName: '',
  practicePath: '',
  firstMajor: '铁道专业',
  secondMajor: '',
  className: '',
  imgKey: '',
  teacherCode: '',
  principalPerson: '',
  practiceType: '虚拟仿真实验',
  practiceIntriduction: ''
})

interface RuleForm {
  practiceName: string,
  practicePath: string,
  firstMajor: string,
  secondMajor: string,
  className: string,
  imgKey: string,
  teacherCode: string,
  principalPerson: string,
  practiceType: string,
  practiceIntriduction: string
}

const rules = reactive<FormRules<RuleForm>>({
  practiceName: [{ required: true, message: '实训课程名称不能为空', trigger: 'blur' }],
  practicePath: [{ required: true, message: '实训课程链接不能为空', trigger: 'blur' }],
  firstMajor: [{ required: true, message: '所属专业类不能为空', trigger: 'blur' }],
  secondMajor: [{ required: true, message: '对应专业不能为空', trigger: 'blur' }],
  className: [{ required: true, message: '对应班级不能为空', trigger: 'blur' }],
  principalPerson: [{ required: true, message: '负责人不能为空', trigger: 'blur' }],
  practiceIntriduction: [{ required: true, message: '实训简介不能为空', trigger: 'blur' }],
  imgKey: [{ required: true, message: '封面图不能为空', trigger: 'blur' }],
})

const uploaded = (res: any) => {
  uploadedImg.value = baseUrl + '/api/file/download/' + res.data;
  form.imgKey = res.data;
}

const submitForm = async (formEl: FormInstance | undefined) => {
  if (!formEl) return
  await formEl.validate(async (valid, fields: any) => {
    if (valid) {
      form.className = form.className.join('、')
      form.secondMajor = form.secondMajor.join('、')
      if (form.id) {
        updatePracticeInfo({ ...form }).then((res: any) => {
          if (res.code == '200') {
            router.back()
          }
        })
        return
      }
      addPracticeInfo({ ...form }).then((res: any) => {
        if (res.code == '200') {
          router.back()
        }
      })
    } else {
      console.log('error submit!', fields)
    }
  })
}

onMounted(() => {
  store.state.pageContentTitle = ['新建实训', '课程信息'];
  const pageQuery: any = router.currentRoute.value.query;
  trainingId.value = +pageQuery.id;
  store.state.pageContentTitle[0] = trainingId.value ? '编辑实训' : '新建实训';
  form.teacherCode = window.localStorage.getItem('userCode') || '';
  getExistSecondMajor().then((res) => {
    majorList.value = [...res.data];
  })
  getExistClass().then((res) => {
    classList.value = [...res.data];
  })
  if (pageQuery.id) {
    queryPracticeInfoById({ id: pageQuery.id }).then((res) => {
      form.practiceName = res.data.practiceName;
      form.practicePath = res.data.practicePath;
      form.firstMajor = res.data.firstMajor;
      form.secondMajor = res.data.secondMajor;
      form.className = res.data.className;
      form.imgKey = res.data.imgKey;
      form.teacherCode = res.data.teacherCode;
      form.principalPerson = res.data.principalPerson;
      form.id = pageQuery.id;
    })
  }
})

</script>
<style scoped lang="less">
.create-wrap {
  padding: 24rem 0 0 208rem;

  .form-box {
    width: 976rem;
    height: 814rem;
    background: #FFFFFF;
    border-radius: 10rem;
    padding: 40rem 271rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    ::v-deep(.el-form-item__label) {
      width: 96rem;
      height: 32rem;
      line-height: 32rem;
      justify-content: flex-end;
      font-size: 14rem;
    }

    ::v-deep(.el-input) {
      width: 330rem;
      font-size: 14rem;
    }

    ::v-deep(.el-input__wrapper) {
      height: 32rem;
    }

    ::v-deep(.el-form-item) {
      margin-bottom: 18rem;
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }

    ::v-deep(.el-textarea) {
      font-size: 14rem;
    }

    ::v-deep(.el-form-item.intriduction-text) {
      align-items: flex-start;
    }

    ::v-deep(.el-form-item__error) {
      font-size: 12rem;
    }

    ::v-deep(.el-select__wrapper) {
      height: 32rem;
      font-size: 14rem;
    }

    ::v-deep(.el-form-item__content) {
      font-size: 0;
      line-height: 1.5;
    }

    .practice-type-text {
      width: 100%;
      height: 100%;
      font-size: 14rem;
      color: #1E1E1E;
    }

    .cover-image-wrap {
      display: flex;
      justify-content: flex-start;
      align-items: flex-end;

      .cover-image {
        position: relative;
        width: 170rem;
        height: 106rem;
        background: #F3F3F3;
        border-radius: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 9rem;

        .img-icon {
          width: 24rem;
          height: 24rem;

          &.full-img {
            width: 100%;
            height: 100%;
          }
        }

        &::before {
          content: '小于2MB的png或jpg图片';
          width: 146rem;
          position: absolute;
          left: 179rem;
          top: 43rem;
          font-weight: 400;
          font-size: 12rem;
          color: #797979;
          line-height: 22rem;
        }
      }

      .upload-btn {
        width: 76rem;
        height: 32rem;
        background: #3991F2;
        box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
        border-radius: 25rem;
        overflow: hidden;

        ::v-deep(.el-upload) {
          width: 100%;
          height: 100%;

          button {
            width: 100%;
            height: 100%;
            background-color: transparent;
            border: none;
            font-size: 14rem;
            color: #fff;
            height: 32rem;
          }
        }
      }
    }

    .form-submit {
      width: 148rem;
      height: 32rem;
      background: #3991F2;
      box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
      border-radius: 25rem;
      margin-left: 155rem;
      margin-top: 40rem;

      .btn-primary {
        width: 100%;
        height: 32rem;
        font-size: 0;

        &>button {
          width: 100%;
          height: 32rem;
          background-color: transparent;
          border-radius: 16rem;
          border: none;
          color: #fff;
          font-weight: 400;
          font-size: 14rem;
          color: #FFFFFF;
          margin-top: 1rem;
        }
      }
    }
  }
}
</style>