<template>
  <div class="home-wrap">
    <div class="create-training">
      <el-upload class="upload-btn" :action="uploadURL" name="teacherExcel" :show-file-list="false"
        :on-success="() => (uploaded('老师'))" :headers="headerObj">
        <el-button>导入老师</el-button>
      </el-upload>
    </div>
    <div class="create-training" style="right: 116rem;">
      <el-upload class="upload-btn" :action="uploadURL2" name="studentExcel" :show-file-list="false"
        :on-success="() => { uploaded('学生') }" :headers="headerObj">
        <el-button>导入学生</el-button>
      </el-upload>
    </div>
    <table-list ref="tableRefs" :delete-dataEvent="deleteDataEvent" :data="tableData" :column-list="columnList"
      :change-page="changePage" :delete-content="deleteContent" :data-total="pageTotal" :page-size="searchForm.pageSize"
      :page-no="searchForm.pageNo" :handle-filter="handleFilter"></table-list>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive } from 'vue'
import TableList from '@/components/TableList.vue'
import store from '@/store'
import { useRouter } from 'vue-router'
import { ElMessage } from "element-plus";
import { getUserPage } from '@/api'

const router = useRouter()
const token = window.localStorage.getItem('token');
const headerObj: any = ref({
  token: token || ''
})
const deleteContent = {
  title: '删除账号',
  content: '确认删除当前账号？'
}
const searchForm: any = reactive({ // 查询对象
  pageSize: 10,
  pageNo: 1
})
const pageTotal = ref(10) // 总计数据条数
const tableRefs = ref()
const tableData: any = ref([])
const baseUrl = process.env.NODE_ENV === "production" ? "http://172.26.136.103:18098" : "";
const uploadURL = ref(baseUrl + '/api/user/uploadTeacherExcel')
const uploadURL2 = ref(baseUrl + '/api/user/uploadStuExcel')

const editData = (data: any) => {
  //
}
const columnList: any = [
  {
    prop: 'userType',
    label: '账号属性',
    list: {
      1: '老师',
      2: '学生'
    },
    filters: [
      { text: '老师', value: 1 },
      { text: '学生', value: 2 }
    ]
  },
  {
    prop: 'firstMajor',
    label: '所属专业类'
  },
  {
    prop: 'secondMajor',
    label: '专业'
  },
  {
    prop: 'className',
    label: '班级'
  },
  {
    prop: 'codeType',
    resetProp: ['stuCode', 'teacherCode'],
    label: '学号/工号/账号'
  },
  {
    prop: 'password',
    label: '密码'
  },
  {
    prop: 'userName',
    label: '姓名'
  }
]

const uploaded = (type: string) => {
  ElMessage({ type: 'success', message: type + '数据导入成功！' })
  searchForm.pageNo = 1;
  getDataList();
}

const deleteDataEvent = (data: any) => {
  //
}

const handleFilter = (key: string, value: any) => {
  searchForm[key] = value ? value[0] : null;
  searchForm.pageNo = 1;
  getDataList();
  tableRefs.value.selectDataArr = null;
}

const changePage = (cur: any) => {
  searchForm.pageNo = cur;
  getDataList()
}

const getDataList = () => {
  getUserPage({ ...searchForm }).then((res) => {
    tableData.value = [...res.data.records];
    pageTotal.value = res.data.total;
  })
}

onMounted(() => {
  store.state.pageContentTitle = ['账号管理'];
  getDataList()
})
</script>
<style scoped lang="less">
.home-wrap {
  position: relative;
  margin-top: 24rem;

  .create-training {
    position: absolute;
    right: 0;
    top: -50rem;
    width: 104rem;
    height: 32rem;
    line-height: 32rem;
    text-align: center;
    background: #3991F2;
    box-shadow: 0px 2px 0px 0px rgba(0, 0, 0, 0.04);
    border-radius: 25rem;
    font-weight: 400;
    color: #FFFFFF;
    cursor: pointer;
    font-size: 0;
    overflow: hidden;

    button {
      width: 104rem;
      height: 32rem;
      font-size: 14rem;
      border: none;
      background-color: transparent;
      color: #fff;
    }
  }
}
</style>