<template>
  <div class="login-page">
    <div class="login-wrap">
      <div class="login-wrap-left"></div>
      <div class="login-wrap-right">
        <div class="login-title">衡水职院虚拟仿真实训管理平台</div>
        <div class="login-tab">
          <div class="title-text">登录</div>
        </div>
        <div class="login-form">
          <el-form ref="ruleFormRef" :model="form" :rules="rules">
            <el-form-item prop="userCode">
              <el-input v-model="form.userCode" placeholder="学生学号/老师工号" :prefix-icon="UserIcon" />
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="form.password" placeholder="密码" :prefix-icon="LockIcon" show-password/>
            </el-form-item>
            <el-form-item class="form-submit">
              <div class="btn-primary">
                <el-button @click="submitForm(ruleFormRef)">确定</el-button>
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { reactive, ref } from 'vue'
import type { FormRules, FormInstance } from 'element-plus'
import UserIcon from '@/components/UserIcon.vue'
import LockIcon from '@/components/LockIcon.vue'
import { useRouter } from 'vue-router'
import store from '@/store'
import { login, getUser } from '@/api'

const router = useRouter()
const ruleFormRef = ref<FormInstance>()
// do not use same name with ref
const form = reactive({
  password: '',
  userCode: '',
})
interface RuleForm {
  password: string,
  userCode: string,
}
const rules = reactive<FormRules<RuleForm>>({
  password: [{ required: true, message: '密码不能为空', trigger: 'blur' }],
  userCode: [{ required: true, message: '学号/工号不能为空', trigger: 'blur' }],
})

const submitForm = async (formEl: FormInstance | undefined) => {
  if (!formEl) return
  await formEl.validate(async (valid, fields) => {
    if (valid) {
      login({ ...form }).then((res) => {
        if (res.data) {
          window.localStorage.setItem('token', res.data)
          window.localStorage.setItem('userCode', form.userCode)
          getUser().then((res1: any) => {
            if (res1.data) {
              const userType = res1.data.userType;
              store.state.userType = userType;
              store.state.userData = { ...res1.data }
              if (userType == 2) {
                router.push('/student-training-list')
              } else if (userType == 1) {
                router.push('/teacher-training-list')
              } else if (userType == 3) {
                router.push('/account-management')
              }
            }
          })
        }
      })

    } else {
      console.log('error submit!', fields)
    }
  })
}
</script>
