  display: flex;
  justify-content: center;
  align-items: center;

  .login-wrap {
    width: 71.46vw;
    height: 41.67vw;
    background-image: url(~@/assets/images/login-bg-w.png);
    background-size: 100% 100%;
    margin-left: -1.04%;
    padding: 0 146rem 0 166rem;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .login-wrap-left {
      width: 312rem;
      height: 278rem;
      background-image: url(~@/assets/images/login-bg.png);
      background-size: 100% 100%;
    }

    .login-wrap-right {
      width: 320rem;
      height: 438rem;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;

      .login-title {
        font-weight: 500;
        font-size: 22rem;
        color: #2A62C8;
        line-height: 28rem;
        margin-bottom: 87rem;
      }

      .login-tab {
        width: 320rem;
        height: 44rem;
        box-sizing: border-box;
        margin-bottom: 32rem;

        &>.title-text {
          width: 34rem;
          font-weight: 500;
          font-size: 16rem;
          color: #2A62C8;
          line-height: 24rem;
          padding: 10rem 0;
          box-shadow: inset 0px -2px 0px 0px #2A62C8;
          text-align: center;
        }
      }

      .login-form {
        width: 320rem;

        .form-submit {
          margin: 32rem 0 0;
          position: relative;
        }

        form>div {
          margin-bottom: 24rem;
        }

        ::v-deep(.el-input__wrapper) {
          padding: 0 11rem;
          border-radius: 6rem;
          box-shadow: none;
          border: 1px solid rgba(0, 0, 0, 0.3);

          .el-input__inner {
            border: none;
            height: 36rem;
