<template>
  <div>
    <base-header v-if="!hideHeader"></base-header>
    <div class="layout" :class="!hideSide ? 'pl216' : 'noPadding'"
      :style="{ height: hideHeader ? '100vh' : 'calc(100vh - 64rem)' }">
      <div class="base_side" v-if="!hideSide">
        <BaseSide :hideSide="hideSide"></BaseSide>
      </div>
      <div class="content">
        <div class="content-box" :style="{ padding: hideHeader ? '0' : '24rem' }">
          <div class="page-content-title" :class="maxPadding ? 'max-padding' : ''" v-if="!hideHeader">
            <div v-for="(item, index) in store.state.pageContentTitle" :key="index">{{ item }}</div>
            <div v-if="maxPadding" class="go-back" @click="router.back()">返回</div>
          </div>
          <router-view />
        </div>
      </div>
    </div>
  </div>
  <tips-view v-show="showTips"></tips-view>
</template>
<script lang="ts" setup>
import { onMounted, ref, reactive, watch, nextTick } from 'vue'
import BaseSide from '@/components/BaseSide.vue';
import BaseHeader from '@/components/BaseHeader.vue';
import { useRouter } from 'vue-router'
import store from '@/store'
import { getUser } from '@/api'
import TipsView from '@/components/TipsView.vue';

const hideSide = ref(true)
const hideHeader = ref(true)
const router = useRouter()
const maxPadding = ref(true)
const showTips = ref(false)

// 路由拦截，这里主要根据路由确定一些组件的显隐性
router.beforeEach((to, from, next) => {
  hideSide.value = Boolean((to.meta && to.meta.hideSide));
  maxPadding.value = Boolean((to.meta && to.meta.maxPadding));
  if (to.path[0] === '/') {
    window.sessionStorage.setItem('path', to.path)
  }
  if (['/login', '/'].includes(to.path)) {
    hideHeader.value = true
  } else {
    hideHeader.value = false
  }
  next();
});

onMounted(() => {
  getUser().then((res1: any) => {
    if (res1.data) {
      store.state.userType = res1.data.userType;
      store.state.userData = { ...res1.data }
    }
  })
  setRem()
  window.addEventListener('resize', () => {
    setRem()
  }, false)
})

const setRem = () => {
  let rootDom: any = document.querySelector('html');
  let rootWidth = document.documentElement.clientWidth || document.body.clientWidth;
  rootDom.style.fontSize = Math.min(Math.max(rootWidth / 1440, 0.2), 3) + 'px';
}

watch(() => store.state.showTips, (newState) => {

  if (newState) {
    nextTick(() => {
      showTips.value = !!(document.querySelectorAll('.tips-model').length == 1)
    })
  }
})
</script>
