<template>
  <div class="side-wrap">
    <el-menu v-if="defaultPath" active-text-color="#1B7AF8" background-color="#FFFFFF" class="el-menu-vertical-demo"
      :default-active="defaultPath" text-color="#798092" :router="true" :unique-opened="true">
      <el-menu-item index="/student-training-list" v-if="userType == 2">
        <el-icon>
          <img src="@/assets/images/icon/train-icon.png" />
        </el-icon>
        <span>实训列表</span>
      </el-menu-item>
      <el-menu-item index="/teacher-training-list" v-if="userType == 1">
        <el-icon>
          <img src="@/assets/images/icon/train-icon.png" />
        </el-icon>
        <span>实训列表</span>
      </el-menu-item>
      <el-menu-item index="/training-results" v-if="userType == 1">
        <el-icon>
          <img src="@/assets/images/icon/score-icon.png" />
        </el-icon>
        <span>实训成绩</span>
      </el-menu-item>
      <el-menu-item index="/account-management" v-if="userType == 3">
        <el-icon>
          <img src="@/assets/images/icon/user-list-icon.png" />
        </el-icon>
        <span>账号管理</span>
      </el-menu-item>
      <el-menu-item index="/user">
        <el-icon>
          <img src="@/assets/images/icon/user-icon.png" />
        </el-icon>
        <span>个人中心</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script lang="ts" setup>
import { onMounted, ref, watch, nextTick } from 'vue'
import store from '@/store'
import { useRouter } from 'vue-router'

const router = useRouter()
const defaultPath = ref('')
const userType = ref(0)
const routerObj: any = {
  '/student-training-list': ['/student-training-list', '/student-details'],
  '/teacher-training-list': ['/teacher-training-list', '/teacher-details'],
  '/training-results': ['/training-results'],
  '/account-management': ['/account-management'],
  '/user': ['/user', '/change-password']
}

onMounted(() => {
  userType.value = store.state.userType;
  const path = window.sessionStorage.getItem('path');
  if (!path) {
    if (userType.value == 2) {
      defaultPath.value = '/student-training-list'
    } else if (userType.value == 1) {
      defaultPath.value = '/teacher-training-list'
    } else if (userType.value == 3) {
      defaultPath.value = '/account-management'
    }
  } else {
    Object.keys(routerObj).forEach((e: any) => {
      if (routerObj[e].includes(path)) {
        defaultPath.value = e;
      }
    })
  }
  if (window.history) {
    history.pushState(null, '', document.URL)
    window.addEventListener('popstate', function () {
      defaultPath.value = '';
      nextTick(() => {
        defaultPath.value = router.currentRoute.value.path
        window.sessionStorage.setItem('path', router.currentRoute.value.path);
      })
    })
  }
})

watch(() => store.state.userType, (newState) => {
  userType.value = newState;
})
</script>
<style scoped lang="less">
::v-deep(.el-menu) {
  background-color: transparent;
  border: none;

  .el-menu-item {
    height: 48rem;
    line-height: 48rem;
    color: #fff;
    border-radius: 4rem;
    margin-bottom: 20rem;
    padding-left: 8rem !important;
    font-size: 14rem;

    &:hover {
      background-color: transparent;
    }

    &.is-active {
      background-color: #3991F2;
    }

    img {
      width: 20rem;
      height: 20rem;
    }
  }

  .el-menu-item [class^=el-icon] {
    margin-right: 10rem;
  }
}
</style>