import request from "./request";

// 查询场景里的素材
export function getUserPage(data: any) {
  return request({
    url: `/user/getUserPage?pageNo=${data.pageNo}&pageSize=${
      data.pageSize
    }&userType=${data.userType || ""}`,
    method: "get",
  });
}

// 登录
export function login(data: any) {
  return request({
    url: `/login?userCode=${data.userCode}&password=${data.password}`,
    method: "post",
  });
}

// 获取用户信息
export function getUser() {
  return request({
    url: "/user/getUser",
    method: "get",
  });
}

// 获取专业名称
export function getExistSecondMajor() {
  return request({
    url: "/user/getExistSecondMajor",
    method: "get",
  });
}

// 获取班级名称
export function getExistClass() {
  return request({
    url: "/user/getExistClass",
    method: "get",
  });
}

// 添加实训
export function addPracticeInfo(data: any) {
  return request({
    url: "/addPracticeInfo",
    method: "post",
    data: data,
  });
}

// 获取教师实训列表
export function queryTeacherPracticeInfo(data: any) {
  return request({
    url: `/queryTeacherPracticeInfo?pageNo=${data.pageNo}&pageSize=${data.pageSize}&teacherCode=${data.userCode}`,
    method: "get",
  });
}

// 获取学生实训列表
export function queryStuPracticeInfo(data: any) {
  return request({
    url: `/queryStuPracticeInfo?pageNo=${data.pageNo}&pageSize=${data.pageSize}&stuCode=${data.userCode}`,
    method: "get",
  });
}

// 获取实训详情
export function queryPracticeInfoById(data: any) {
  return request({
    url: `/queryPracticeInfoById?id=${data.id}`,
    method: "get",
  });
}

// 更新实训
export function updatePracticeInfo(data: any) {
  return request({
    url: "/updatePracticeInfo",
    method: "post",
    data: data,
  });
}

// 删除实训
export function deletePracticeInfo(data: any) {
  return request({
    url: "/deletePracticeInfo?id=" + data.id,
    method: "post",
  });
}

// 修改密码
export function modifyUserPassword(data: any) {
  return request({
    url: `/user/modifyUserPassword?userId=${data.userId}&password=${data.password}`,
    method: "post",
  });
}

// 获取需要带过去的token
export function getPracticeRelateInfoStr(data: any) {
  return request({
    url: "getPracticeRelateInfoStr?practiceId=" + data.practiceId,
    method: "get",
  });
}

// 获取实训名称
export function getPracticeNames() {
  return request({
    url: "getPracticeNames",
    method: "get",
  });
}
